import React, { Fragment } from 'react'
import { Provider } from 'react-redux'
// import { syncHistoryWithStore } from 'react-router-redux'

// 1. updates state , does not change url, error : TypeError: Cannot read property 'type' of undef
// import { Router } from 'react-router-dom'

// 2. Element type is invalid: expected a string (for built-in components) ... index.js
import { ConnectedRouter } from 'react-router-redux'

// 3. updates state , does not change url, error : TypeError: Cannot read property 'type' of undef
// import { Router } from 'react-router'
import { history as browserHistory } from './helpers/history'

import store from './store'


import './App.scss'

import { Routes } from './components/routes'
import { Footer } from './components/footer'
import { Header } from './components/header'

const App = () =>
  (
    <div className="App">
      <Provider store={store}>
        <ConnectedRouter history={browserHistory}>
          <Fragment>
            <Header />
            <Routes />
            <Footer bgTemplate="template1" />
          </Fragment>
        </ConnectedRouter>
      </Provider>
    </div>)

export default App



